body {
  font-family: Arial, sans-serif;
}

.container {
  border: 1px solid #ccc;
  border-radius: 10px;
  max-width: 800px;
  margin: 20px auto;
  padding: 20px;
  box-shadow: 0 0 10px #0000001a;
}

input[type="text"] {
  width: calc(100% - 80px);
  padding: 10px;
  font-size: 16px;
}

input[type="submit"] {
  cursor: pointer;
  width: 80px;
  padding: 10px;
  font-size: 16px;
}

.forecast {
  margin-top: 20px;
  display: flex;
  overflow-x: auto;
}

.day {
  text-align: center;
  cursor: pointer;
  border: 1px solid #ccc;
  border-radius: 10px;
  flex: 1;
  margin: 0 5px;
  padding: 10px;
}

.day:hover {
  background-color: #f0f0f0;
}

.weather-info {
  margin-top: 20px;
}

.hide {
  display: none;
}

.currentforecast img {
  width: 100px;
  height: 100px;
}

.currentforecast p {
  margin: 5px 0;
}
